body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background: #616161;
  background: -moz-linear-gradient(90deg, #CCCCCC 0%, #8C8C8C 100%);
  background: -webkit-linear-gradient(90deg, #CCCCCC 0%, #8C8C8C 100%);
  background: linear-gradient(90deg, #CCCCCC 0%, #8C8C8C 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#616161",endColorstr="#b5b5b5",GradientType=1);
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  text-transform: uppercase;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.background {
  position: fixed;
  top: 0;
  z-index: -1000;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(to top, #387aff 0%, #081483 100%);
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.boton-der-blanco {
  display: flex;
  justify-content: right;
  align-items: flex-end;
  text-align: right;
  flex-direction: column;
}
.boton-der-blanco a {
  background-color: #FFFFFF;
  color: #000000;
  padding: 0.5rem;
  border-radius: 0.5rem;
}

.paginadores {
  display: flex;
  justify-content: space-around;
}

.boton-siguiente {
  width: 10rem;
  font-size: 3rem;
  font-weight: bold;
}

.header {
  display: flex;
  justify-content: space-around;
  text-align: center;
  align-items: center;
  background-image: linear-gradient(to top, #FF530D 0%, #E82C0C 50%, #FF0000 100%);
}
.header .header-logo {
  height: 15rem;
  width: auto;
  margin: 0;
}
.header .titulo {
  color: #000000;
}

.tablaStock {
  width: 100%;
  background-color: darkkhaki;
  border: 1px solid black;
  margin: 5rem 0;
  border-collapse: collapse;
}
.tablaStock caption {
  font-size: 5rem;
  font-weight: bold;
  background-color: gray;
}
.tablaStock thead {
  background-color: red;
}
.tablaStock th, .tablaStock td {
  border: 2px solid black;
}

.navegacion {
  position: fixed;
  top: 0;
  right: 0;
  width: 30vw;
  height: 100%;
  background-image: linear-gradient(to right, #FF530D 0%, #E82C0C 50%, #FF0000 100%);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  transition: transform 0.8s ease-in-out;
  transform: translate(110%);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
}
.navegacion a {
  color: whitesmoke;
  text-decoration: none;
}

.spread {
  transform: translate(0);
}

.icono-menu {
  background: #FF530D;
  width: 40px;
  height: 40px;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 0;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
}